import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import "./index.css"
import JSONData from "../data/home.json"
import Carousel, { CarouselItem } from "../components/Carousel"
import ThankYouLetter from "../components/thankYouLetter"
import { StaticImage } from "gatsby-plugin-image"
import { Link } from "gatsby"

const IndexPage = () => {
  return (
    <Layout page="home">
      <Seo title="Home" />
      <section className="section blue-bg">
        <div className="home-widthLimit">
          <div className="columns">
            <div className="column is-two-thirds has-text-centered">
              <div className="container">
                <Carousel>
                  {JSONData.homeCarousel.map(data => <CarouselItem><ThankYouLetter src={data.image} alt={data.alt} className="home-images" /></CarouselItem>)}
                </Carousel>
              </div>
            </div>
            <div className="column">
              <div className="home-title-container">
                <h1 className="home-title">12 YEARS OF</h1>
                <h1 className="home-title">VOLUNTEER</h1>
                <h1 className="home-title">EXPERIENCE</h1>
                <strong className="home-subtitle">AND IT IS ONLY OUR START</strong>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="section">
        <div className="home-widthLimit">
          <div className="columns">
            <div className="column is-half has-text-centered">
              <div className="container">
                <StaticImage src="../images/logo.png" alt="Community Improvement Service" className="home-logo" />
              </div>
            </div>
            <div className="column">
              <div className="home-title-container2">
                <Link to="/about-us/our-journey"><strong className="home-about-us">ABOUT US</strong></Link>
                <strong className="home-about-us-description">We are a non-profit organization located in San Francisco.</strong>
                <span className="home-about-us-description2">Lead youth and young adults to serve the community, develop their leadership skills and guide them to explore their career of interests.</span>
              </div>
            </div>
          </div>
        </div>
      </section>
      {
        JSONData.upcomingEvents.toString() ? <section className="home-section-title">
          <strong className="home-section-title-text">Upcoming Events</strong>
        </section> : ""
      }
      {
        JSONData.upcomingEvents.map((data, index) => {
          return (
            <section className={`section${index % 2 === 0 ? " home-colored-section" : ""}`}>
              <div className="home-widthLimit">
                <div className="columns">
                  {
                    index % 2 === 0 ?
                      <div className="column is-three-fifths">
                        {data.resources.map(data => {
                          if (data.hasOwnProperty("image")) {
                            return (data.image.map(data => (
                              <ThankYouLetter src={data.image} alt={data.alt} style={{maxWidth: 542}} />
                            )))
                          }
                          if (data.hasOwnProperty("videoLinks")) {
                            return (data.videoLinks.map(data => (
                              <iframe width="560" height="315" src={data.link}
                                      title={data.title} frameBorder="0"
                                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                      allowFullScreen/>
                            )))
                          }
                          return ""
                        })}
                      </div>
                      :
                      <div className="column">
                        <strong className="home-event-title">
                          {data.title}
                        </strong>
                        <span className="home-event-description">
                          {data.description}
                        </span>
                      </div>
                  }
                  {
                    index % 2 === 0 ?
                      <div className="column">
                        <strong className="home-event-title">
                          {data.title}
                        </strong>
                        <span className="home-event-description">
                          {data.description}
                        </span>
                      </div>
                      :
                      <div className="column is-three-fifths">
                        {data.resources.map(data => {
                          if (data.hasOwnProperty("image")) {
                            return (data.image.map(data => (
                              <ThankYouLetter src={data.image} alt={data.alt} style={{maxWidth: 542, float: "right"}} />
                            )))
                          }
                          if (data.hasOwnProperty("videoLinks")) {
                            return (data.videoLinks.map(data => (
                              <iframe width="560" height="315" src={data.link}
                                      title={data.title} frameBorder="0"
                                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                      allowFullScreen/>
                            )))
                          }
                          return ""
                        })}
                      </div>
                  }
                </div>
              </div>
            </section>
          )
        })
      }
      {
        JSONData.recentEvents.toString() ? <section className="home-section-title">
          <strong className="home-section-title-text">Recent Events</strong>
        </section> : ""
      }
      {
        JSONData.recentEvents.map((data, index) => {
          return (
            <section className={`section${index % 2 === 0 ? " home-colored-section" : ""}`}>
              <div className="home-widthLimit">
                <div className="columns">
                  {
                    index % 2 === 0 ?
                      <div className="column is-three-fifths">
                        {data.resources.map(data => {
                          if (data.hasOwnProperty("image")) {
                            return (data.image.map(data => (
                              <ThankYouLetter src={data.image} alt={data.alt} style={{maxWidth: 542}} />
                            )))
                          }
                          if (data.hasOwnProperty("videoLinks")) {
                            return (data.videoLinks.map(data => (
                              <iframe width="560" height="315" src={data.link}
                                title={data.title} frameBorder="0"
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen
                                className="home-videos"
                              />
                            )))
                          }
                          return ""
                        })}
                      </div>
                      :
                      <div className="column">
                        <strong className="home-event-title">
                          {data.title}
                        </strong>
                        <span className="home-event-description">
                          {data.description}
                        </span>
                      </div>
                  }
                  {
                    index % 2 === 0 ?
                      <div className="column">
                        <strong className="home-event-title">
                          {data.title}
                        </strong>
                        <span className="home-event-description">
                          {data.description}
                        </span>
                      </div>
                      :
                      <div className="column is-three-fifths">
                        {data.resources.map(data => {
                          if (data.hasOwnProperty("image")) {
                            return (data.image.map(data => (
                              <ThankYouLetter src={data.image} alt={data.alt} style={{maxWidth: 542}} className="home-images-right" />
                            )))
                          }
                          if (data.hasOwnProperty("videoLinks")) {
                            return (data.videoLinks.map(data => (
                              <iframe width="560" height="315" src={data.link}
                                      title={data.title} frameBorder="0"
                                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                      allowFullScreen
                                      className="home-videos"
                              />
                            )))
                          }
                          return ""
                        })}
                      </div>
                  }
                </div>
              </div>
            </section>
          )
        })
      }
      <section className="home-section-title">
        <strong className="home-section-title-text">What&nbsp;&nbsp;We&nbsp;&nbsp;Do</strong>
      </section>
      <section className="home-colored-section">
        <div className="home-widthLimit">
          <div className="columns has-text-centered" style={{paddingBottom: 128, paddingTop: 24}}>
            <div className="column">
              <span className="home-what-we-do-text">CIS SELF-DEVELOPED PROJECTS</span>
            </div>
            <div className="column">
              <span className="home-what-we-do-text">EVENT ASSISTANCE</span>
            </div>
            <div className="column">
              <span className="home-what-we-do-text">CAREER EXPLORATION</span>
            </div>
            <div className="column">
              <Link to="/get-involved/join-us" className="home-what-we-do-link">JOIN US</Link><span className="home-what-we-do-text"> AND EXPLORE MORE OPPORTUNITIES!</span>
            </div>
          </div>
        </div>
      </section>
      <section className="section" />
    </Layout>
  )
}

export default IndexPage
